  /** @ngInject */
  function DvSupport() {

    return {
      CSVtoJSON: CSVtoJSON
    };

    function CSVtoJSON(csvString) {
      var papaData = Papa.parse(csvString);
      return papaData.errors.length == 0 ? papaData.data : null;
    }
  }
